      align-items: center; }
      @media (max-width: 768px) {
        .about__details--story3 {
          flex-direction: column-reverse; } }
  .about__footer {
    padding-top: 200px;
    padding-bottom: 10px;
    margin: 0px auto;
    display: flex;
    flex-direction: column;
    align-items: center; }
  .about__general {
