.about-image {
  max-width: 90vw; }

.article {
  max-width: 100vw; }

.article-about {
  padding-top: 100px;
  padding-bottom: 100px;
  display: grid;
  grid-template-columns: 800px 1fr;
  grid-template-rows: 680px 1fr;
  grid-column-gap: 5px; }
  @media (max-width: 768px) {
    .article-about {
      grid-template-columns: 1fr; } }
  .article-about__box {
    display: flex;
    flex-direction: column;
    justify-content: center; }
    @media (max-width: 768px) {
      .article-about__box {
        order: 1; } }
  .article-about__photo {
    display: flex;
    justify-content: center;
    align-content: center; }
    @media (max-width: 768px) {
      .article-about__photo {
        order: 2; } }
    .article-about__photo--placement {
      width: 600px;
      height: 600px;
      background: white;
      align-self: center;
      box-sizing: border-box; }

.article-collection {
  padding-top: 100px;
  padding-bottom: 100px;
  display: grid;
  grid-template-columns: 1fr 700px;
  grid-template-rows: 1fr 650px;
  grid-column-gap: 5px; }
  @media (max-width: 768px) {
    .article-collection {
      grid-template-columns: 1fr; } }
  .article-collection__box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center; }
  .article-collection__description {
    max-width: 400px;
    overflow: hidden;
    border-bottom: 1px #f6bb01 solid;
    padding-bottom: 10px;
    align-self: center; }
  .article-collection__photo {
    display: flex;
    justify-content: center;
    align-content: center; }
  .article-collection__placement {
    width: 600px;
    height: 600px;
    background: white;
    align-self: center;
    box-sizing: border-box; }
  .article-collection__title {
    color: black;
    font-size: 35px;
    align-self: center; }

.article-item {
  padding-top: 100px;
  padding-bottom: 100px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 800px;
  grid-column-gap: 16px; }
  @media (max-width: 768px) {
    .article-item {
      grid-template-columns: 1fr; } }
  .article-item__box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .article-item__photo {
    width: 600px;
    height: 600px;
    box-sizing: border-box;
    background: white;
    padding: 0px;
    display: flex; }
    @media (max-width: 768px) {
