        order: 2; } }
  .article-item__paragraph {
    border-bottom: 1px #f6bb01 solid; }
  .article-item__description {
    padding: 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center; }
    @media (max-width: 768px) {
