.contact-site {
  padding-top: 6%;
  display: grid;
  grid-template-areas: "header header" "about content";
  grid-column-gap: 16px; }
  .contact-site__header {
    margin: 0px auto;
    grid-area: header; }
  @media (max-width: 768px) {
    .contact-site {
      grid-template-areas: "header" "content" "about"; } }

.contact {
  margin: 0px auto;
  grid-area: about; }
  .contact__box {
    grid-area: content; }

.contact-form {
  padding-left: 0px; }
  .contact-form__textarea {
